.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}

.services-titel{
    position: relative;

}
.services-titel h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}

.services-titel img{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.services-format{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 20px;
    padding: 60px;
    border-radius: 10px;
    border: 2px solid white;
    transition: 0.4s;
    cursor: pointer;
    height: 100%;
    min-height: 450px;
    box-sizing: border-box;
}


.services-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-bottom: 80px;
    align-items: stretch;
    grid-auto-rows: 1fr;
}


.services-format h3{
    font-size: 28px;
    font-weight: 600;
}

.services-format h2{
    font-size: 38px;
    font-weight: 800;
    background: linear-gradient(267deg,#DA7C25 50%,#B923E1 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.services-format p{
    color: #d4d4d4;
    font-size: 24px;
    line-height: 40px;
    max-width: 100%;
    flex: 1;
    min-height: 0;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    line-clamp: 4;
    -webkit-box-orient: vertical;
}

.services-readmore{
    display: flex;
    gap:20px;
    align-items: center;
    margin-top: auto;
}


.services-format:hover{
    border: 2px solid #ff0ff0;
    background: linear-gradient(45deg,#3f0028 ,#582300);
    transform: scale(1.05);
    transition: 0.4s;
} 

@media(max-width:768px){
    .services{
        align-items: start;
        gap: 70px;
        margin: 20px 70px;
    }
    
    
    .services-titel h1{
        padding: 0px ;
        font-size: 50px;
    }
    
    .services-titel img{

        width: 130px;
        margin-right: -20px;
    }
    
    .services-format{
        gap: 20px;
        padding: 32px;
        min-height: 400px;
        box-sizing: border-box;
    }
    
    .services-format h2{
        font-size: 28px;

    }
    
    .services-format p{
        font-size: 18px;
        line-height: 32px;
        max-width: 100%;
        flex: 1;
        min-height: 0;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        line-clamp: 4;
        -webkit-box-orient: vertical;
    }
    
    .services-readmore img{
      width: 20px;
    }
    
    .services-container{
        grid-template-columns:1fr 1fr ;
        gap: 30px;
        align-items: stretch;
        grid-auto-rows: 1fr;
    }
    

}